'use server';

import { z } from 'zod';
import { addFileToStore, deleteFileFromStore, getLatestFileMetadataFromStore, type StoredFile } from '@/lib/file-store';
import { revalidatePath } from 'next/cache';

const MAX_FILE_SIZE = 5 * 1024 * 1024; // 5MB limit for uploaded JSON file
const MAX_COOKIE_JSON_CONTENT_SIZE = 100 * 1024; // 100KB limit for the content of the JSON

const UploadResponseSchema = z.object({
  success: z.boolean(),
  fileId: z.string().optional(),
  fileName: z.string().optional(),
  error: z.string().optional(),
});
export type UploadResponse = z.infer<typeof UploadResponseSchema>;

export async function uploadFile(
  prevState: UploadResponse,
  formData: FormData
): Promise<UploadResponse> {
  const file = formData.get('cookieFile') as File | null;

  if (!file) {
    return { success: false, error: 'No file selected for upload.' };
  }

  if (file.type !== 'application/json') {
    return { success: false, error: 'Invalid file type. Only .json files are allowed.' };
  }

  if (file.size > MAX_FILE_SIZE) {
    return { success: false, error: `File size exceeds ${MAX_FILE_SIZE / (1024 * 1024)}MB limit.` };
  }

  let fileContent: string;
  try {
    fileContent = await file.text();
  } catch (e) {
    console.error('Error reading file content:', e);
    return { success: false, error: 'Could not read file content.' };
  }
  
  if (new TextEncoder().encode(fileContent).length > MAX_COOKIE_JSON_CONTENT_SIZE) {
    return { success: false, error: `JSON content size exceeds ${MAX_COOKIE_JSON_CONTENT_SIZE / 1024}KB limit.` };
  }

  try {
    // Validate if content is valid JSON
    JSON.parse(fileContent);
  } catch (e) {
    return { success: false, error: 'File content is not valid JSON.' };
  }

  try {
    const storedFile = addFileToStore({
      fileName: file.name,
      content: fileContent,
      mimeType: file.type,
      size: file.size,
    });
    
    revalidatePath('/'); 
    // Revalidate the specific download page as well, if you have one.
    // revalidatePath(`/download/${storedFile.id}`); // This was for a specific page, might not be needed now
    return { success: true, fileId: storedFile.id, fileName: storedFile.fileName };
  } catch (e) {
    console.error('File upload processing error:', e);
    const errorMessage = e instanceof Error ? e.message : 'An unexpected error occurred during file processing.';
    return { success: false, error: errorMessage };
  }
}

const DeleteResponseSchema = z.object({
  success: z.boolean(),
  message: z.string().optional(),
  error: z.string().optional(),
});
export type DeleteResponse = z.infer<typeof DeleteResponseSchema>;

export async function deleteFileAction(
  prevState: DeleteResponse,
  formData: FormData 
): Promise<DeleteResponse> {
  const fileId = formData.get('fileId') as string | null;

  if (!fileId) {
    return { success: false, error: 'File ID is missing.' };
  }

  try {
    const deleted = deleteFileFromStore(fileId);
    if (deleted) {
      revalidatePath('/'); 
      // revalidatePath(`/download/${fileId}`); 
      return { success: true, message: 'Shared session file deleted successfully.' };
    } else {
      return { success: false, error: 'Shared session file not found or already deleted.' };
    }
  } catch (e) {
    console.error('Shared session file deletion error:', e);
    const errorMessage = e instanceof Error ? e.message : 'An unexpected error occurred.';
    return { success: false, error: `Error during deletion: ${errorMessage}` };
  }
}

const FileMetadataResponseSchema = z.object({
    file: z.custom<Omit<StoredFile, 'content'>>().nullable(),
    error: z.string().optional(),
});
export type FileMetadataResponse = z.infer<typeof FileMetadataResponseSchema>;

export async function getLatestSharedFileMetadataAction(): Promise<FileMetadataResponse> {
  try {
    const fileMetadata = getLatestFileMetadataFromStore();
    return { file: fileMetadata || null };
  } catch (e) {
    console.error('Error fetching latest shared file metadata:', e);
    const errorMessage = e instanceof Error ? e.message : 'An unexpected error occurred.';
    return { file: null, error: `Failed to fetch latest shared file information: ${errorMessage}` };
  }
}
